@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&family=Oswald:wght@200;300;400;500;600;700&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  box-sizing: border-box;
}

body {
  background: #f7f7ef;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.adminContainer {
  margin-left: 250px;
}

nav {
  background: #808000;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 5%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 10;
}

.topbarToggler > svg,
.topbarCart > svg {
  color: #fff !important;
  font-size: 2rem !important;
}

.topbarToggler {
  display: none;
}

.topbarLogo {
  display: flex;
}
.topbarLogo > img {
  height: 40px;
  margin: 0 10px 0 0;
}

nav > ul {
  display: flex;
  justify-content: space-around;
  padding: 0 10%;
  width: 100%;
}
nav > ul > li {
  list-style: none;
}
nav > ul > li > a {
  font-family: Oswald;
  font-weight: 500;
  font-size: 1rem;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
  transition: color 0.5s;
}

nav > ul > li > a:hover,
nav > ul > li > a.active {
  color: #fff075;
  border-bottom: 3px solid #fff075;
}

.help {
  position: relative;
}

.helpList {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 50px;
  background: #ffffff;
  box-shadow: 0px 8px 33px rgba(0, 0, 0, 0.05), 0px 5px 16px rgba(0, 0, 0, 0.035);
  border-radius: 5px;
  width: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 0.2s ease;
}
.helpList.active {
  opacity: 1;
  pointer-events: auto;
}

.helpList li {
  width: 100%;
  list-style: none;
  padding: 0.3rem 0;
}
.helpList li:hover {
  background: #f1ece8;
  border-radius: 5px;
}

.helpList li > a {
  padding: 0.5rem;
  font-family: Nunito Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 1rem;
  line-height: 25px;
  letter-spacing: 0.04em;
  text-transform: capitalize;
  color: #636363;
  text-decoration: none;
}

.topbarShortcut {
  display: flex;
  justify-content: flex-end;
  height: 100%;
}

.topbarCart {
  width: 100%;
  height: 25px;
  margin-top: 15px;
  display: flex;
  align-items: center;
  padding-right: 15px;
  margin-right: 10px;
  border-right: 2px solid #e1e1e1;
  text-decoration: none;
  position: relative;
}
.topbarCart span {
  position: absolute;
  top: -10px;
  left: 20px;
  background: tomato;
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  font-family: Nunito Sans;
  font-weight: bold;
  color: #fff;
  font-size: 12px;
}

@media screen and (min-width: 1024px) {
  nav > ul {
    padding: 0 20%;
  }
}

@media screen and (max-width: 768px) {
  .topbarToggler {
    display: block;
  }

  .topbarLogo > img {
    height: 30px;
    margin: 0 5px;
  }

  nav > ul {
    position: absolute;
    left: 0;
    top: 60px;
    background: #b9b93c;
    flex-direction: column;
    align-items: center;
    transform: translateX(-100%);
    transition: transform 0.5s ease;
  }

  nav li {
    padding: 1rem;
    display: flex;
    justify-content: center;
    width: 100%;
  }

  nav > ul.active {
    transform: translateX(0);
  }
}

.speedDial {
  margin-top: 10px;
}

.speedDial > .MuiFab-root {
  width: 0;
  height: 0;
}

.speedDialIcon {
  width: 40px;
  height: 40px;
  border-radius: 100%;
}

.footer {
  width: 100%;
  padding: 0 5%;
  overflow: hidden;
}
.upperFooter {
  padding: 5rem;
  border-top: 1px solid #a57f81;
  border-bottom: 1px solid #a57f81;
}
.upperFooter > h1 {
  font-family: Oswald;
  font-weight: 600;
  font-size: 2.5rem;
  text-align: center;
  letter-spacing: 0.04em;
  color: #1a1a1a;
  padding: 1rem;
}
.upperFooter > ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.upperFooter > ul > li {
  font-family: Nunito Sans;
  font-size: 1.2rem;
  text-align: center;
  line-height: 2rem;
  letter-spacing: 0.04em;
}

.lowerFooter {
  display: flex;
  position: relative;
  z-index: 2;
  background: linear-gradient(
    180deg,
    rgba(128, 128, 0, 0) 50.15%,
    rgba(128, 128, 0, 0.6) 217.47%
  );
  margin: 0 -9999rem;
  padding: 5% calc(5% + 9999rem);
}
.lowerFooter > div {
  flex: 1 1;
}
.lowerFooter > div h2 {
  font-family: Oswald;
  font-weight: 600;
  font-size: 1.4rem;
  line-height: 2rem;
  letter-spacing: 0.04em;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  color: #1a1a1a;
}
.lowerFooter > div p {
  font-family: Nunito Sans;
  font-weight: 600;
  font-size: 1rem;
  line-height: 2rem;
  letter-spacing: 0.04em;
  color: #636363;
}
.lowerFooter > div:nth-child(1) {
  text-align: left;
}
.lowerFooter > div:nth-child(1) p {
  display: flex;
  align-items: center;
  margin: 1rem 0;
}
.lowerFooter > div:nth-child(1) p > svg {
  margin-right: 10px;
}
.lowerFooter > div:nth-child(2) {
  text-align: center;
}
.lowerFooter > div:nth-child(3) {
  text-align: right;
}
.footerLogo > img {
  height: 40px;
  margin: 0 10px;
}

.copyrightFooter {
  width: 100%;
  padding: 1rem 5%;
  background: #808000;
  color: #fff;
}
.copyrightFooter > p {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Nunito Sans;
  font-size: 1.2rem;
  line-height: 25px;
  text-align: center;
  letter-spacing: 0.04em;
}
.copyrightFooter > p > svg {
  margin-right: 10px;
}

@media screen and (max-width: 992px) {
  .lowerFooter {
    flex-direction: column;
  }
  .lowerFooter > div:nth-child(1) {
    text-align: center;
  }
  .lowerFooter > div:nth-child(1) p {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .lowerFooter > div:nth-child(2) {
    text-align: center;
    margin: 1rem 0;
  }
  .lowerFooter > div:nth-child(3) {
    text-align: center;
  }
}

.notFoundPage {
  height: 100vmin;
  width: 100vw;
  max-width: 100%;
  background: linear-gradient(
    180deg,
    rgba(128, 128, 0, 0.16) -3.21%,
    rgba(128, 128, 0, 0) 54.13%
  );
}
.notFoundPage::after {
  content: "";
  width: 100vw;
  height: 100vmin;
  background: #d9d9b9;
  position: absolute;
  top: 0;
  left: 0;
  max-width: 100%;
  z-index: -1;

  -webkit-clip-path: polygon(0 82%, 100% 58%, 100% 100%, 0% 100%);

          clip-path: polygon(0 82%, 100% 58%, 100% 100%, 0% 100%);
}

.notFoundHeader {
  display: flex;
  padding: 20px 10vmax;
}
.notFoundHeader > img {
  height: 40px;
  margin: 0 10px;
}

.notFoundLeaf {
  width: 100%;
  display: flex;
  position: absolute;
  top: 0;
}
.notFoundLeaf > img:first-child {
  width: 5vmax;
}
.notFoundLeaf > img:last-child {
  width: 7vmax;
  margin-left: auto;
}

.notFoundContainer {
  height: calc(100vmin - 120px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.notFoundContainer h1,
.notFoundContainer h2 {
  font-family: "Oswald";
}
.notFoundContainer h1 {
  font-size: 5rem;
}
.notFoundContainer h2 {
  font-size: 2rem;
}
.notFoundContainer p {
  font-family: "Nunito Sans";
  color: #636363;
}
.notFoundContainer img {
  width: 40vmax;
}

@media screen and (max-width: 768px) {
  .notFoundHeader {
    display: flex;
    justify-content: center;
    padding: 20px 0;
  }
  .notFoundPage::after {
    display: none;
  }

  .notFoundContainer {
    height: 80vh;
  }
  .notFoundContainer img {
    display: none;
  }
}

