  width: 100vw;
  height: calc(100vh - 60px);
  max-width: 100%;
  z-index: 1;
}
.sectionOnePage > img {
  pointer-events: none;
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.sectionOneContainer {
  padding: 0 5%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: calc(100% - 140px);
}

.sectionOneHeader {
  margin-bottom: 3rem;
}

.sectionOneHeader > h1 {
  font-family: Oswald;
  font-size: 5rem;
  font-style: normal;
  font-weight: 600;
  line-height: 5rem;
